        p La arquitectura de un sistema IoT es la estructura que define cómo los dispositivos, redes y plataformas de gestión de datos interactúan para capturar, procesar y actuar sobre la información. Entender esta arquitectura es crucial para diseñar e implementar soluciones IoT eficientes que se adapten a las necesidades específicas de un proceso productivo. En general, un sistema IoT se organiza en tres capas: percepción, red y aplicación. Cada una de estas capas cumple una función específica en el flujo de datos y en la operación del sistema.  
        .bg9.p-4.brad
