},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-8 my-lg-0 my-3"},[_c('p',[_vm._v("Otra ventaja clave es la reducción de costos, que se logra a través de la optimización de recursos como el agua, la energía y los insumos. Al monitorear y ajustar los procesos con precisión, IoT minimiza el desperdicio y maximiza el rendimiento de los recursos, promoviendo la sostenibilidad. Además, los sistemas IoT permiten un mantenimiento predictivo, identificando posibles fallos antes de que se produzcan y reduciendo así los costos asociados con paradas imprevistas o reparaciones de emergencia.")]),_c('div',{staticClass:"bg2 p-4 brad"},[_c('p',{staticClass:"mb-0"},[_vm._v("IoT también facilita la toma de decisiones basada en datos y análisis, lo que permite a los usuarios desarrollar estrategias más informadas y efectivas. Esta ventaja es fundamental en la gestión de procesos productivos complejos, donde una visión clara y actualizada del sistema permite mejorar la productividad y mantener altos estándares de calidad. En este apartado, los estudiantes aprenderán a valorar estas ventajas y a identificar cómo pueden aplicarlas en el desarrollo y optimización de sistemas productivos en sus propios contextos de trabajo.")])])]),_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/27.png"),"alt":""}})])])
